@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;700;800&display=swap');

* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
  background: #F7F5EE;
  color: #000000;
}

button {
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}
